* {
  /* border: 1px black solid; */

}

.App {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

div {
  display: flex;
  /* border: 1px black solid; */
}

ul {
  display: inline;
  margin: 0;
  padding: 0;

  list-style: none;
}

li {
  float: left;
  background: #eee;
  margin-left: -20px;
  margin-bottom: -40px;
}

li:first-child {
  margin-left: 0px
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

